*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
 
  color: #000;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}


img {
  display: block;
  max-width: 100%;
  height: auto;
}

.title{
    display: grid;
    place-items: center;
    font-size: 40px;
}

.cards__info{
    display: flex;
    justify-content: center;
    align-items: center;
    
    padding: 20px;

}

.cards__items{
   font-size: 30px;
margin-right: 40px;

}

.image{
    border: 4px solid #000;
}



.search{
    display: flex;
    justify-content: left;
    align-items: center;

    padding: 60px;
}

.input{
    height: 30px;
    width: 200px;
    margin-right: 5px;

    border: 2px solid rgb(12, 231, 231);
    border-radius: 5px;
}

.button{
    height: 30px;

     border: none;
      background-color: rgb(12, 231, 231);
      color: #fff;
    border-radius: 5px;
}
.button:hover{
    background-color: #fff;
      color: rgb(12, 231, 231);
      border: 1px solid #000;
}
